
.navbar{
	background-color: #F9CD09!important;
	padding-top: 10px;
	border:transparent;
}

.navbar li{
  font-size: 14px;   
}

.navbar .navbar-nav>.active>.nav-link{
  color:#CF387A;
  font-weight: bold;	
  font-size: 16px
}

.navbar-light .navbar-nav .nav-link{
  color: #ffffff;
  font-weight: bold;
}

.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{
  color:#CF387A;
}

