.content-pasos-prestamo{
  height: 100%;
  width: 80%;
  margin: auto;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:space-between;
  align-items:center;
  padding: 5px 25px;
}
.paso1, .paso2, .paso3, .paso4, .paso5{
background-color: #CF387A;
color: #ffffff;
width: 50px;
